.InfoCard {
  display: flex;
  flex-direction: column;
  background-color: var(--cardColor);
  padding: 1rem;
  border-radius: 1rem;
  width: 90%;
  margin: 1rem 0;
}
.infoHead {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
}
.infoHead > div:hover {
  cursor: pointer;
}
.logout-button {
  width: 7rem;
  height: 2rem;
  margin-top: 6rem;
  align-self: flex-end;
}
.info {
  margin: 5px 1rem;
}
